import React, { Component } from "react";
import InputDate from "../Components/inputDate";
import { getAllCountries } from "../Services/travelCosts";
import Option from "../Components/selects";
import { allNBPCurrency } from "../Services/servicurrency";
import Form from "../Components/Form";
import Expense from "./expenditure";
import ExpenseEditor from "./expenseEditor";
export default class Highorder extends Component {
  constructor(props) {
    super(props);
    this.state = {
      allCountries: [],
      selectValue: "eur",
      startDate: "",
      startTime:"",
      stopDate: "",
      stopTime:"",
      bid: 0,
      diet: 0,
      accommodation: 0,
      country: {},
      countryData: {},
      currency: "",

      name: "Jaroslaw",
      surname: "Lucjan",
      rodzaj_wydatku: "No transport",
      kasa: 0,
      test_Array: [1, 2],
      expenses: [],
      tempDeleteIndex: 0,
      showStyle: { visibility: "hidden" },
    };
  }
  addExpense = (name) => {
    let tempExpenses = [...this.state.expenses];
    console.log("Funkcja przeslana z parametrem name: ", name);
    tempExpenses.push(name);
    this.setState({ expenses: tempExpenses });
  };
  deleteExpense = (event) => {
    let objectID = event.target.id;
    let tempArray = this.state.expenses;
    tempArray.splice(objectID, 1);
    this.setState({ expenses: tempArray });
  };

  showEditor = () => {
    this.setState({ showStyle: styleButton2 });
  };
  hideEditor = () => {
    this.setState({ showStyle: styleButton1 });
  };

  handleStateChange = (event) => {
    let name = event.target.name;
    var cc = event.target.value;
    this.setState({ [name]: cc });
  };
  componentDidMount() {
    getAllCountries().then((country) => {
      this.setState({ allCountries: country });
      console.log(this.state.allCountries);
    });
    allNBPCurrency(this.state.selectValue).then((element) => {
      console.log("Currency get from BNP", element);
      this.setState({ bid: element.rates[0].bid });
    });
  }

  // Zmiana w dropdown liscie
  handleChange = (event) => {
    var cc = event.target;
    var test = new Promise(function (resolve, reject) {
      console.error("Sprawdzam teraz tablice wszystkich krajw");
      resolve("First promise done");
    });
    test
      .then((result) => {
        const foundCountry = this.state.allCountries.find((element) => {
          if (cc.value === element.code) {
            console.log("Znalazlem element w tablicy!", element);
            return element;
          }
        });
        this.setState({ country: foundCountry });
        return foundCountry;
      })
      .then((result) => {
        console.log("A tutaj w drugim then: ", result);
        allNBPCurrency(result.currency).then((element) => {
          console.log("Data from NBP:", element);
          this.setState({
            bid: element.rates[0].bid,
            currency: result.currency,
          });
        });
      });
  };

  testHandleDiffDate = () => {
    // To set two dates to two variables

    // to jest dla delegacji polskiej
    let data1= this.state.startDate;
    let time1= this.state.startTime;
    let data2= this.state.stopDate;
    let time2= this.state.stopTime;
    var date1 = new Date( data1+" "+time1+":00");
    var date1a = new Date(data1+" 23:59:59");
    var date2a = new Date(data2+" 00:00:00");
    var date3a = new Date(data2+" "+time2+":00");
    console.log("Data start 1 dzien:", date1);
    console.log("Data koniec 1 dzien:", date1a);
    var Difference_In_Time1 = date1a.getTime() - date1.getTime();
    var Difference_In_Days1a =( Difference_In_Time1 / (1000 * 3600 * 24)).toFixed(2);
    console.warn("Roznica czasu to: ",Difference_In_Days1a);
    console.log("Data start przedzial:", date1a);
    console.log("Data stop przedzial:", date2a);
    var Difference_In_Time2 = date2a.getTime() - date1a.getTime();
        var Difference_In_Days2a = (Difference_In_Time2 / (1000 * 3600 * 24)).toFixed(0);
    console.warn("Roznica czasu to: ",Difference_In_Days2a);
    console.log("Data start koniec:", date2a);
    console.log("Data stop koniec:", date3a);
    var Difference_In_Time3 = date3a.getTime() - date2a.getTime();
    var Difference_In_Days3a = (Difference_In_Time3 / (1000 * 3600 * 24)).toFixed(2);
    console.warn("Roznica czasu to: ",Difference_In_Days3a);


    //Licz od następnego dnia od ogidzny 00 do ostatniego ostatniego dnia do godziny 00

    // Licz od początku ostatniego do konca jesli w PL
    //Licz od początk dnia do przyjazd  do PL jeśli poza PL
    //Licz od przyjazdu do konca delegacji jesl ibyles poza PL i wróciłeś

    // To calculate the time difference of two dates

    
    // To calculate the no. of days between two dates
    // var Difference_In_Days1a = Difference_In_Time1a / (1000 * 3600 * 24);
    // var Difference_In_Days = Difference_In_Time / (1000 * 3600 * 24);
   
  };
  handleInputStartDate = (event) => {
    this.setState({ startDate: event.target.value });
  };
  render() {
    if (this.state.allCountries.length === 0) {
      return <div>Lol pusto teraz</div>;
    } else {
      const CountiesSelect = this.state.allCountries.map((element) => {
        return <Option code={element.code} country={element.country} />;
      });

     
      const expensesArray = this.state.expenses.map((element, index) => {
        return (
          <div>
            {" "}
            typ: {element.type}, data: {element.date}, kwota: {element.amount}{" "}
            zł
            <button id={index} onClick={this.deleteExpense}>
              Usuń
            </button>{" "}
            <button id={index} onClick={this.deleteExpense}>
              Edit
            </button>
          </div>
        );
      });

      return (
        <div className="container-contact">
          <h1>Lista wydatków:</h1>
          {expensesArray}
          <br />
          <button onClick={this.showEditor}>Dodaj wydatek</button>
          <button onClick={this.testHandleDiffDate}>Sprewadz date</button>

          <br />
          <ExpenseEditor
            addEvent={this.addExpense}
            style={this.state.showStyle}
            hideEditor={this.hideEditor}
          />
          Krajowy
          <InputDate nameDate = "startDate" nameTime="startTime" handleChange={this.handleStateChange} />
          Krajowy to data: {this.state.startDate} czas: {this.state.startTime}
          <InputDate nameDate = "stopDate" nameTime="stopTime" handleChange={this.handleStateChange} />
          Krajowy to data: {this.state.stopDate} czas: {this.state.stopTime}
          <br/>
          <select id="selColor" onChange={this.handleChange}>
            {CountiesSelect}
          </select>
          <h3>
            Nazwisko: {this.state.surname} <br />
            Imie: {this.state.name}
          </h3>
          <Form
            name="name"
            surname="surname"
            handleStateChange={this.handleStateChange}
          />
          <p>Sprawdzamy jak wystawic i dodac wydatki:</p>
          <Expense
            name="kasa"
            handleStateChange={this.handleStateChange}
            onChangeList={this.handleStateChange}
            objName="rodzaj_wydatku"
          />
          <div style={divStyle}>
            Imie: {this.state.name}
            <br />
            Nazwisko: {this.state.surname}
            <br />
            Wydatek: {this.state.kasa}
            <br />
            Rodzaj wydatku:: {this.state.rodzaj_wydatku}
            <br />
          </div>
        </div>
      );
    }
  }
}
const divStyle = {
  color: "yellow",
  background: "red",
  width: "100%",
  height: "100px",
};

const styleButton1 = {
  visibility: "hidden",
};
const styleButton2 = {
  visibility: "visible",
};
